<template>
  <section id="aboutSmall">
    <div class="info">
      <div class="info__carousel padding" v-for="item in aboutInfo" :key="item.name">
        <transition-group name="slider" tag="p">
            <span class="info__text" v-for="el in item.text" :key="el.message" v-show="showSlider">
              {{ el.message }}
            </span>
        </transition-group>
      </div>
    </div>
    <div class="slash" @click="nextSlider()"><img src="../assets/media/slash.png" alt="-" class="slash__img"></div>
  </section>
</template>

<script>
export default {
  name: "About",
  data() {
    return {
      aboutInfo: [
        {
          name: 'expert',
          text: [{message: '      1998 .'}, {message: '50  '}, {message: '  VIP-: , , , , '}, {message: ' >200   '}]
        },
        {
          name: 'company',
          text: [{message: '  ,      .'}, {message: '   ,      '}, {message: 'Call-,       .'}, {message: '      .'}]
        },
        {name: 'third', text: [{message: '1'}, {message: '2'}, {message: '3'}]}
      ],
      showSlider: true,
    }
  },
  mounted: function () {
    setInterval(() => {
      this.showSlider = !this.showSlider;
      this.nextSlider();
      setTimeout(() => {
        this.showSlider = !this.showSlider
      }, 2000);
    }, 10000);
  },
  methods: {
    nextSlider: function () {
      let arr = [];
      this.aboutInfo.forEach((item, index) => {
        if (index === this.aboutInfo.length - 1) {
          arr[0] = item;
        } else {
          arr[index + 1] = item;
        }
      })
      return this.aboutInfo = arr;
    }
  },
}
</script>

<style scoped>
.slider-enter {
  transition: all .1ms ease;
  opacity: 0;
}

.slider-enter-to {
  transition: all .5s linear;
  opacity: 100;
}

.slider-leave-active {
  transition: all .5s linear;
  opacity: 0;
  transform: translateX(-100px);
}

