  background-color: white;
  min-width: 100vw;
}

.info__text {
  display: flex;
  justify-content: center;
  font-size: 1.5rem;
  text-align: center;
  //margin: 15px 0;
}

.slash__img {
  min-width: 100%;
}

@media (max-width: 350px) {
  .slash__img {
    max-width: 100%;
  }
}
</style>